// components/MasonryTextos.tsx
export default function MasonryTextos({ textos }: { textos: Array<{ titulo: string; contenido: string }> }) {
  return (
    <div className="columns-1 md:columns-2 lg:columns-3 gap-6 space-y-6 max-w-6xl mx-auto">
      {textos.map((texto, index) => (
        <div key={index} className="break-inside-avoid bg-white p-5 rounded-lg shadow-md border">
          <h3 className="font-semibold mb-2 text-gray-800">{texto.titulo}</h3>
          <p className="text-sm text-gray-600 leading-relaxed">{texto.contenido}</p>
        </div>
      ))}
    </div>
  );
}