// app/plaqueta/page.tsx
import { textosPlaqueta } from './plaqueta';
import GaleriaTextos from './GaleriaTextos.tsx';

export default function PlaquetaPage() {
  return (
    <div className="min-h-screen bg-gray-50 py-12">
      <div className="container mx-auto px-4">
        <header className="text-center mb-12">
          <h1 className="text-4xl font-bold mb-4">Título de la Plaqueta</h1>
          <p className="text-xl text-gray-600">Una colección de 11 textos breves</p>
        </header>
        
        <GaleriaTextos textos={textosPlaqueta} />
        
        {/* Opcional: Descarga PDF */}
        <div className="text-center mt-12">
          <a 
            href="/path/to/plaqueta.pdf" 
            download
            className="inline-flex items-center px-6 py-3 border border-gray-300 rounded-lg hover:bg-gray-50"
          >
            📄 Descargar versión PDF
          </a>
        </div>
      </div>
    </div>
  );
}